
import {defineComponent} from "vue"

export default defineComponent({
  name: "c-text-button",
  props: {
    size: {
      type: String,
      default: "common",
      validator(value: string) {
        return ["small", "common"].includes(value)
      }
    },
    unactive: {
      type: Boolean,
      default: false
    },
    capitalize: {
      type: Boolean,
      default: false
    },
    doublet: {
      type: Boolean,
      default: false
    }
  }
})
